<template>
  <div class="page">
    <index-top 
      :h1="'KISwap'"
      :h4="$t('index.最具潜力的集DEX、IKO、DAO、聚合交易为一体的DEFI生态平台')"></index-top>
    <div class="top-text">
      {{$t('liquidity.注意:我们现在只支持Heco，不要转移ETHEREUM mainnet令牌')}}
    </div>
    <div class="pool-block flex-wrap">
      <div class="pool-box" v-show="!addDialogVisible && !minsDialogVisible">
        <div class="tit">{{$t('liquidity.未领取奖励')}}(SSC)</div>
        <div class="cont" v-if="address">{{userInfo.canWithdraw}}</div>
        <template v-else>
          <img src="~@/assets/image/light/liquidity/lock.png" alt="" class="icon-lock" v-if="themeMode === 'light'">
          <img src="~@/assets/image/liquidity/lock.png" alt="" class="icon-lock" v-else>
        </template>
        <div class="btns flex-center">
          <div @click="withdrawMdx" class="btn flex-item" :class="{'disabled': !address}">{{$t('liquidity.收取奖励')}}</div>
          <!-- <div class="btn handle-btn flex-center"><img src="~@/assets/image/liquidity/add.png" alt=""></div> -->
        </div>
      </div>
      <div class="pool-box" v-show="!addDialogVisible && !minsDialogVisible">
        <div class="tit" v-if="!address">{{$t('liquidity.KISwap流动性质押')}}({{poolInfo.tokenSymbol}})</div>
        <div class="tit" v-else>{{$t('liquidity.已质押')}}({{poolInfo.tokenSymbol}})</div>
        <div class="cont" v-if="address">{{Number(userInfo.amount).toFixed(6)}}</div>
        <template v-else>
          <img src="~@/assets/image/light/liquidity/lock.png" alt="" class="icon-lock" v-if="themeMode === 'light'">
          <img src="~@/assets/image/liquidity/lock.png" alt="" class="icon-lock" v-else>
        </template>
        <div class="btns flex-center">
          <div class="btn flex-item" v-if="!address" @click="initWallet">{{$t('liquidity.链接钱包')}}</div>
          <div class="btn flex-item" v-else-if="!Number(tokenAllowance)" @click="approveToken">{{$t('liquidity.授权',{symbol: poolInfo.tokenSymbol})}}</div>
          <template v-else>
             <div class="btn handle-btn flex-center" @click="addDialogVisible = true">
               <!-- <img src="~@/assets/image/light/liquidity/add.png" alt="" v-if="themeMode === 'light'">
               <img src="~@/assets/image/liquidity/add.png" alt="" v-else> -->
               {{$t('pool.质押')}}
             </div>
             <div class="btn handle-btn flex-center" @click="minsDialogVisible = true">
               <!-- <img src="~@/assets/image/light/liquidity/mins.png" alt="" v-if="themeMode === 'light'">
               <img src="~@/assets/image/liquidity/mins.png" alt="" v-else> -->
               {{$t('pool.解锁')}}
             </div>
          </template>
          <!-- <div class="btn flex-item" v-else @click="deposit">{{$t('liquidity.质押',{symbol: poolInfo.tokenSymbol})}}</div> -->
          
        </div>
      </div>
    </div>
    <!-- 质押 -->
    <el-dialog custom-class="pool-dialog" :title="`${$t('pool.质押')} ${poolInfo.tokenSymbol} Tokens`" v-model="addDialogVisible">
      <div class="dialog-box">
        <div class="input-block flex-center">
          <input type="number" v-model="addAmount" class="input-style flex-item" :placeholder="$t('liquidity.请输入数量')">
          <div class="coin-style">{{poolInfo.tokenSymbol}}</div>
          <div class="btn-style" @click="addAmount = userInfo.tokenBlance">{{$t('liquidity.最大值')}}</div>
        </div>
        <div class="input-msg">{{userInfo.tokenBlance}}{{poolInfo.tokenSymbol}}{{$t('liquidity.可用')}}</div>
        <div class="btns-group flex-center">
          <div class="btn flex-item" @click="deposit">{{$t('pool.质押')}}</div>
          <div class="btn flex-item" @click="addDialogVisible = false">{{$t('liquidity.取消')}}</div>
        </div>
      </div>
    </el-dialog>
    <!-- 解押 -->
    <el-dialog custom-class="pool-dialog" :title="`${$t('pool.解押')} ${poolInfo.tokenSymbol} Tokens`" v-model="minsDialogVisible">
      <div class="dialog-box">
        <div class="input-block flex-center">
          <input type="number" v-model="reduceAmount" class="input-style flex-item" :placeholder="$t('liquidity.请输入数量')">
          <div class="coin-style">{{poolInfo.tokenSymbol}}</div>
          <div class="btn-style" @click="reduceAmount = userInfo.amount">{{$t('liquidity.最大值')}}</div>
        </div>
        <div class="input-msg">{{userInfo.amount}}{{poolInfo.tokenSymbol}}{{$t('liquidity.可用')}}</div>
        <div class="btns-group flex-center">
          <div class="btn flex-item" @click="withdraw">{{$t('pool.解押')}}</div>
          <div class="btn flex-item" @click="minsDialogVisible = false">{{$t('liquidity.取消')}}</div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import IndexTop from "@/views/index/IndexTop";
import poolList from "@/utils/poolList";
import { reactive, toRefs, computed, watch } from "vue";
import { useRoute } from "vue-router";
import {useStore} from "vuex";
import { MaxUint256 } from "@ethersproject/constants";
import web3Data from "@/utils/initWeb3";
import poolAbi from "@/utils/poolAbi";
import tokenAbi from "@/utils/abi";
import BigNumber from "bignumber.js";
import { ElMessage } from 'element-plus';
import config from "@/utils/config";
import { useI18n } from "vue-i18n";
export default {
  components: {
    IndexTop
  },
  setup () {
     const route = useRoute();
     const store = useStore();
     const { t } = useI18n();
     const data = reactive({
       $t: t,
       addDialogVisible: false,
       minsDialogVisible: false,
       themeMode: computed(() => store.state.themeMode),
       poolAddress: config.poolAddress,
       tokenAllowance: 0,
       address: computed(() => store.state.accounts[0]),
       poolPairList: poolList,
       web3: computed(() => store.state.provider),
       poolContract: null,
       tokenContract: null,
       totalAllocPoint: 0,
       addAmount: '',
       reduceAmount: '',
       poolData: {
         totalAmount: 0,
         allocPoint: 0,
         lastRewardBlock: 0
       },
       userInfo: {
         rewardDebt: 0,
         amount: 0,
         canWithdraw: 0,
         tokenBlance: 0
       },
       pid: Number(route.params.pid),
       poolInfo: computed(() => {
          let pool = data.poolPairList.filter((item) => item.pid == data.pid)
          if (pool.length) {
            return pool[0]
          }
          return {}
       }),
       initWallet: () => {
         web3Data.initWeb3()
       },
       initData: () => {
         data.poolContract = new data.web3.eth.Contract(poolAbi, data.poolAddress)
         data.tokenContract = new data.web3.eth.Contract(tokenAbi, data.poolInfo.lpAddresses)
         data.initInfo()
       },
       initInfo: () => {
         data.getTokenAllowance()
         data.getPoolInfo()
         data.getUserInfo()
         data.getPendingMdx()
         data.getTokenBlance()
       }, 
       getTokenAllowance: () => {
        data.tokenContract.methods.allowance(data.address, data.poolAddress).call().then((amount) => {
          data.tokenAllowance = amount;
        })
       },
       approveToken: () => {
         data.tokenContract.methods.approve(data.poolAddress, MaxUint256).send({from: data.address}).then((result) => {
          data.getTokenAllowance()
        })
       },
       getPendingMdx: () => {
         data.poolContract.methods.pending(data.poolInfo.pid, data.address).call().then((result) => {
           console.log(data.web3.utils)
           data.userInfo.canWithdraw = Number(result['0']/ Math.pow(10, 18)).toFixed(6)
         })
       },
       getPoolInfo: () => {
         data.poolContract.methods.poolInfo(data.poolInfo.pid).call().then(async (result) => {
            data.poolData.allocPoint = result.allocPoint
            data.poolData.totalAmount = result.totalAmount
            data.poolData.lastRewardBlock = result.lastRewardBlock
         })
       },
       getUserInfo: async () => {
         let decimals = await data.tokenContract.methods.decimals().call();
         data.poolContract.methods.userInfo(data.poolInfo.pid, data.address).call().then((result) => {
            data.userInfo.rewardDebt = result.rewardDebt
            data.userInfo.amount = new BigNumber(result.amount).dividedBy(Math.pow(10, decimals)).toFixed(Number(decimals))
         })
       },
       getTokenBlance: async () => {
         let tokenBlance = await data.tokenContract.methods.balanceOf(data.address).call()
         let decimals = await data.tokenContract.methods.decimals().call();
         console.log(tokenBlance, decimals)
         data.userInfo.tokenBlance = new BigNumber(tokenBlance).dividedBy(Math.pow(10, decimals)).toFixed(Number(decimals))
       }, 
       deposit: async () => {
         if (Number(data.userInfo.tokenBlance) > 0 && Number(data.userInfo.tokenBlance) >= Number(data.addAmount)) {
           data.addDialogVisible = false;
           let decimals = await data.tokenContract.methods.decimals().call();
           let amount = new BigNumber(data.addAmount).multipliedBy(Math.pow(10, decimals)).toFixed();
           data.poolContract.methods.deposit(data.poolInfo.pid, amount).send({from: data.address}).then((result) => {
             data.initInfo()
           })
         } else {
            ElMessage.error(data.$t('liquidity.数量不足'));
         }
       },
       withdraw: async () => {
         if (Number(data.userInfo.amount) > 0 && Number(data.userInfo.amount) >= Number(data.reduceAmount)) {
           data.minsDialogVisible = false;
           let decimals = await data.tokenContract.methods.decimals().call();
           let amount = new BigNumber(data.reduceAmount).multipliedBy(Math.pow(10, decimals)).toFixed();
           data.poolContract.methods.withdraw(data.poolInfo.pid,amount).send({from: data.address}).then((result) => {
            data.initInfo()
         })
         }  else {
              ElMessage.error(data.$t('liquidity.数量不足'));
         }
       },
       withdrawMdx: () => {
          data.poolContract.methods.deposit(data.poolInfo.pid, '0').send({from: data.address}).then((result) => {
             data.initInfo()
           })
       }
     })
     if (data.address) {
        data.initData()
     }
     watch(() => data.address, (val) => {
       if (val) {
         data.initData()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss">
.pool-dialog {
  width: 550px !important;
  height: 433px;
  margin: auto !important;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: url('~@/assets/image/liquidity/border4.png') no-repeat 0 0;
  background-size: 100% 100%;
  padding: 50px 69px;
  color: #fff;
  .el-dialog__headerbtn {
    top: 58px;
    right: 53px;
    .el-dialog__close {
      color: #fff;
      font-size: 28px;
    }
  }
  .el-dialog__body {
    padding: 45px 0 0;
  }
  .el-dialog__header {
    padding: 10px 20px;
    text-align: center;
    .el-dialog__title {
      color: #fff;
      font-weight: bold;
      font-size: 20px;
    }
  }
  .dialog-box {
    .input-block {
      background: #fff;
      color: #333333;
      font-size: 20px;
      height: 70px;
      .input-style {
        width: 100%;
        height: 40px;
        border: 0;
        padding: 0 25px;
        font-size: 20px;
        color: #333;
      }
      .coin-style {
        margin-right: 27px;
      }
      .btn-style {
        margin-right: 14px;
        font-weight: bold;
        color: #188277;
      }
    }
    .input-msg {
      font-size: 18px;
      color: #188277;
      text-align: right;
      margin: 18px 0;
    }
    .btns-group {
      margin: 60px 0 0;
      .btn+.btn {
        margin-left: 37px;
      }
      .btn {
        max-width: 240px;
        height: 54px;
        margin: 0 auto;
        background: url('~@/assets/image/liquidity/btn_bg5.png') no-repeat 0 0;
        background-size: 100% 100%;
        font-size: 18px;
        color: #fff;
        font-weight: bold;
        line-height: 54px;
        text-align: center;
        transition: all 0.3s;
        cursor: pointer;
        &:hover {
          background-image: url('~@/assets/image/liquidity/btn_bg6.png');
        }
      }
    }
  }
}
.theme-light {
  .pool-dialog {
    box-shadow: none;
    background-image: url('~@/assets/image/light/liquidity/border4.png');
    .dialog-box {
      .input-block {
        .btn-style {
          color: #3A7343;
        }
      }
      .input-msg {
        color: #3A7343;
      }
      .btns-group {
        .btn {
          color: #3A7343;
          background-image: url('~@/assets/image/light/liquidity/btn_bg5.png');
          &:hover {
            color: #AD7805;
            background-image: url('~@/assets/image/light/liquidity/btn_bg6.png');
          }
        }
      }
    }
  }
}
@media screen and (max-width: 480px) {
  .pool-dialog {
    width: 100% !important;
    height: 296px;
    background-size: contain;
    padding: 20px 30px;
    .el-dialog__headerbtn {
      top: 27px;
      right: 25px;
      .el-dialog__close {
        font-size: 26px;
      }
    }
    .el-dialog__header {
      .el-dialog__title {
        font-size: 16px;
      }
    }
    .el-dialog__body {
      padding: 15px 0 0;
    }
    .dialog-box {
      .input-block {
        height: 44px;
        font-size: 14px;
        .input-style {
          height: 30px;
          padding: 0 12px;
          font-size: 14px;
        }
        .coin-style {
          margin-right: 12px;
        }
      }
      .input-msg {
        font-size: 13px;
        margin: 8px 0;
      }
      .btns-group {
        margin: 40px 0 0;
        .btn+.btn {
          margin-left: 20px;
        }
        .btn {
          height: 44px;
          line-height: 44px;
          font-size: 16px;
        }
      }
    }
  }
}
</style>
<style lang="scss" scoped>
